
* {
  box-sizing: border-box;
}

body {
  background-color: black;
}

h1, h2 {
  font-family: "Open Sans", serif;
}

h3 {
  font-family: Helvetica, sans-serif;
}

a {
  text-decoration: none;
  font-family: sans-serif;
}

/***********
** HEADER **
***********/


.section-background-image {
  height: 270px;
  /*height: 40vh;*/
  width: 100vw;
  background: url("../img/stars2.jpg") no-repeat;
  background-size: cover;
  /*background-position: center bottom;*/
  border-bottom: 1px solid #333;
}

  .section-image-alpha{
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    padding-top: 50px;
  }

    .logo {
      margin: 0 auto;
      margin-bottom: 30px;
      text-align: center;

    }

    .logo img {
      text-align: center;
      width: 300px;
    }

    nav ul {
      display: flex;
      justify-content: space-around;
      /*align-content: flex-end;*/
      color: white;
    }

    nav ul li {

    }

    nav ul a {
      font-size: 20px;
      color: white;
      padding: 10px;
      border: 2px solid #333;
      transition: all 0.3s ease;
    }

    nav ul a:hover {
      background-color: #333;
    }

    .fixed-nav {
      z-index: 9999;
      position: fixed;
      left: 0;
      top: 0;
      width: 100%;
      padding: 20px 0;
      background-color: black;
      border-bottom: 1px solid #333;
    }


/*********
** MAIN **
*********/


main{
  min-height: 500px;
}

main section, main.planet-section {

  width: 100%;
  text-align: center;

}


main section h1 {
  padding: 30px 0;
  font-size: 60px;
  color: white;
}

input {
  padding: 5px 0 5px 0;
  margin-bottom: 20px;
  font-size: 16px;
}

.post {
  width: 100%;
  /*height: 400px;*/
  background-color: white;
  border: 1px solid black;
  margin: 10px 0;
  border-radius: 10px;
}

  .post img {
    width: 100%;
  }

  .post p {
    padding: 16px;

  }

  .post h2 {
    text-align: center;
    font-size: 55px;
    padding-bottom: 10px;
  }
  .post ul {
    display: block;
    width: 200px;
    margin: 0 auto;
    /*border: solid 1px black;*/
    font-size: 18px;
  }

  .post li {
    padding: 5px;
  }
  .post h2 {
    font-size: 45px;
    padding-bottom: 20px;
  }

  .post .stats {
    text-align: center;
  }

  .post .stats-title {
    font-weight: bold;
  }

  .stats li {
    padding: 3px;
  }

  #postTime h2{
    font-size: 55px;
  }

/*.planet-name {
  font-size: 55px;
}*/

#postTime {
  height: 200px;
  /*padding-top: 30px;*/
  display: flex;
  align-items: center;
  justify-content: center;
  /*padding: 50px;*/
}

#postTime p {
  text-align: center;
  font-size: 30px;
  padding: 0;
  margin-top: 20px;
}




/*tablet size screens*/
@media (min-width: 568px){

  body {
    /*background-color:white;*/
  }

  /***********
  ** HEADER **
  ***********/


  .section-background-image {
    height: 375px;
  }

  .logo img {
    text-align: left;
    width: 500px;
  }
    nav ul {
      max-width: 900px;
      margin: 0 auto;
    }


  /*********
  ** MAIN **
  *********/


  main {
    width: 90%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    /*background-color: white;*/
  }

  main section h1 {
    font-size: 80px;
  }

    .post {
      width: 48%;
    }


    #postTime {
      padding-top: 0px;
    }

    #postTime p {
      font-size: 40px;
    }


} /* end tablet size screens */



/*desktop size screens*/
@media (min-width: 1200px){
  body {
    /*background-color: ;*/
  }

  /***********
  ** HEADER **
  ***********/

  .section-background-image {
    height: 510px;
  }

  .logo img {

    width: 770px;
  }

  /*********
  ** MAIN **
  *********/

  main {
    width: 88%;
    max-width: 1200px;
    margin: 0 auto;
  }

  main section h1 {
    font-size: 100px;
  }

    .post {
      width: 32%;
    }

    #postTime {
      width:48%;
    }
} /* end desktop size screens */
